.Cart {
    height: 70px;
    width: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 100px;
    right: 50px;
    border-radius: 50%;
    border: 3px solid #000000;
    box-shadow: 0px 5px 10px rgba(100, 0, 0, 0.6);

    background-color: red;
    color: #fff;

    transition: .3s ease-in-out;
    cursor: pointer;
}

.Cart:hover {
    transform: scale(.95);
}

@media screen and (max-width:500px) {
    .Cart {
        right: 10px;
    }
}